import React from "react";
import "../css/AboutMe.css";
import ContactMe from "./ContactMe";
import Education from "./Education";
import Technology from "./Technology";
import "../fonts/Ubuntu-Light.ttf";
import profilePic from "../images/profile-pic.jpg";

class AboutMe extends React.Component {

  componentDidMount() {
    document.getElementById('body').style.background = "#FFFFFF";
  }

  render() {
    return(
      <div id="about-me" className="text-center">
        <div>

          <div id="intro-section" className="row">
            <div id="intro-summary" className="col-xs-5">
              <h1 id="about-me-title">About Me</h1>
              <p id="about-me-summary" className="text-left">I am a recent graduate with a strong background in mathematics and a passion for problem-solving. I thrive on the challenge of learning new technologies quickly and strive to continually improve my abilities as a developer. In my free time, I enjoy playing the guitar, reading, and cooking.</p>
            </div>
            <div className="col-xs-4">
              <img id="profile-pic" src={profilePic} alt="Andrew Hatch" className="img-responsive"/>
            </div>
          </div>
        </div>
        <Education />
        <Technology />
        <ContactMe />
      </div>
    )
  }
}

export default AboutMe;
