import React from "react";
import "../css/Welcome.css";
import { Link } from "react-router-dom";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faArrowRight } from "@fortawesome/free-solid-svg-icons";
import "../fonts/Boogaloo-Regular.ttf";
import "../fonts/Ubuntu-Light.ttf";

class Welcome extends React.Component {

  componentDidMount() {
    document.getElementById('body').style.background = "linear-gradient(180deg, #000000 10%, #FFFFFF)";
  }

  render() {
    return(
      <div id="welcome-section">
        <h1 id="welcome-title">Hi.</h1>
        <p id="welcome-intro">I'm Andrew.</p>
        <p id="welcome-summary">I am a detail-oriented software developer with a passion for creating intuitive and responsive applications. I am currently seeking opportunities in front-end development in the Boston area.</p>
        <Link id="projects-link" to="/projects" onClick={() => document.getElementById('body').style.background = "#FFFFFF"}><button id="button-link" className="btn">View my work <FontAwesomeIcon icon={faArrowRight} /></button></Link>
      </div>
    );
  }
};

export default Welcome;
